.header-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 70%;
  margin-bottom: 20px;
}
.header-container .header-logo-bar {
  width: 100%;
  margin-top: 20px;
  padding-bottom: 20px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.header-container .header-logo-bar__logo {
  width: 300px;
  cursor: pointer;
}
.header-container .header-logo-bar__button {
  margin: 0 10px;
  padding: 20px;
  background: none;
  font-size: 14px;
  border: none;
  cursor: pointer;
}
.header-container .carousel-container {
  position: relative;
  height: 200px;
  width: 1200px;
  display: flex;
  justify-content: flex-start;
  overflow: hidden;
}
.header-container .carousel-container .baner-bg {
  width: 1200px;
  height: 100%;
}
.header-container .carousel-container .baner-bg1 {
  background-image: url(../../img/baner1.png);
  position: absolute;
}
.header-container .carousel-container .baner-bg2 {
  background-image: url(../../img/baner2.png);
  position: absolute;
}
.header-container .carousel-container .baner-bg3 {
  background-image: url(../../img/baner3.png);
  position: absolute;
}
.header-container .carousel-container .arrow-container {
  position: absolute;
  top: 50%;
  height: 100%;
  width: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translateY(-50%);
  background-color: rgba(0, 0, 0, 0.2);
  box-shadow: 0 0 20px -10px;
  z-index: 1;
  cursor: pointer;
  transition: 0.2s ease-out;
}
.header-container .carousel-container .arrow-container .arrow-icon {
  color: white;
  font-size: 40px;
  cursor: pointer;
  user-select: none;
  z-index: -2;
}
.header-container .carousel-container .arrow-container-left {
  left: 0px;
}
.header-container .carousel-container .arrow-container-right {
  right: 0px;
}
.header-container .carousel-container .arrow-container:hover {
  background-color: rgba(0, 0, 0, 0.4);
}

@media (max-width: 1300px) {
  .header-container {
    width: 90%;
  }
  .header-container .carousel-container {
    position: relative;
    height: 200px;
    width: 100%;
    display: flex;
    justify-content: flex-start;
    overflow: hidden;
  }
  .header-container .carousel-container .baner-bg {
    width: 100%;
    height: 100%;
  }
}
@media (max-width: 992px) {
  .header-container {
    width: 95%;
  }
  .header-container .header-logo-bar {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
  }
  .header-container .header-logo-bar__logo {
    padding-left: 10px;
    margin-bottom: 10px;
  }
  .header-container .header-logo-bar__button {
    padding: 10px;
  }
  .header-container .carousel-container {
    width: 100%;
  }
  .header-container .carousel-container .baner-bg {
    width: 100%;
    display: flex;
    justify-content: center;
  }
  .header-container .carousel-container .baner-bg1 {
    background-image: url(../../img/baner1mobile.png);
    background-repeat: no-repeat;
    background-position: center;
  }
  .header-container .carousel-container .baner-bg2 {
    background-image: url(../../img/baner2mobile.png);
    background-repeat: no-repeat;
    background-position: center;
  }
  .header-container .carousel-container .baner-bg3 {
    background-image: url(../../img/baner3mobile.png);
    background-repeat: no-repeat;
    background-position: center;
  }
}